import * as React from 'react';
import { graphql } from 'gatsby';

const Page = () => <></>;

export const query = graphql`
  query {
    site {
      siteMetadata {
        siteUrl
      }
    }
  }
`;

export default Page;

export const Head = props => {
  const {
    data: {
      site: {
        siteMetadata: { siteUrl }
      }
    }
  } = props;

  return (
    <>
      <meta name='robots' content='noindex' />
      <meta httpEquiv='Refresh' content={`0; URL=${siteUrl}`} />
    </>
  );
};
